
@charset "UTF-8";
/*头部医生信息样式--head*/
body {
  overflow: hidden;
}
.checkRecord {
  overflow: hidden;
}
#info {
  height: 2rem;
  overflow: hidden;
  border-bottom: solid 1px #DDDDDD;
  padding-left: 280px;
  position: relative;
}
#info img {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  margin-top: 0.5rem;
  margin-left: 0.5rem;
  float: left;
}
.outpatient-part {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 100%;
  width: 20%;
  vertical-align: middle;
  height: 1.0rem;
  padding-top: 0.05rem;
  text-align: center;
  line-height: 0.4rem;
  color: #0b55a3;
  display: inline-block;
}
.outpatient-part span {
    display: block;
    font-size: 0.25rem;
}
.info-right {
  height: 2rem;
  width: 60%;
  padding-left: .5rem;
  padding-top: .4rem;
  padding-bottom: .5rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
}
.checkRecord .back .head {
  top: 0.15rem !important;
  left: 160px;
  position: relative;
}
.checkRecord .back .back {
  left: 0 !important;
  position: relative;
}
.info-right-top,
.info-right-bottom {
  height: 0.5rem;
}
.info-right-top > div:last-child {
  /*width: 1.5rem;*/
}
.littleinfobox {
  float: left;
  font-size: 0.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.infotip {
  height: 100%;
  margin: 0 0.2rem;
  line-height: 0.5rem;
  color: #acacac;
  font-size: 0.3rem;
}
.inforeal {
  height: 100%;
  margin: 0 0.2rem;
  line-height: 0.5rem;
  color: #333;
  width: 2.2rem;
  font-size: .28rem;
  font-weight: normal;
}
.inforeal2 {
  height: 100%;
  margin: 0 0.2rem;
  line-height: 0.5rem;
  color: #333;
  font-size: .28rem;
  /*width: 1rem;*/
  font-weight: normal;
}
.inforeal3 {
  height: 100%;
  margin: 0 0.2rem;
  line-height: 0.5rem;
  float: left;
  color: #333;
  font-size: .28rem;
  width: 1.8rem;
  font-weight: normal;
}
.printing {
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  right: 4%;
  top: 10.9%;
  padding: .1rem 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 2px solid #3C9BFF;
  border-radius: .24rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  vertical-align: middle;
}
.printing img {
    width: .9rem;
    border-radius: 0 !important;
    margin-top: 0rem !important;
    margin-left: 0rem !important;
}
.printing P {
    font-family: PingFangSC-Medium;
    font-size: .28rem;
    color: #3C9BFF;
    letter-spacing: 0;
    text-align: center;
}
/*头部医生信息样式--end*/
.checkRecord-introduce {
  margin-top: 0.25rem;
  padding-left: 160px;
}
.introduce-child {
  padding: 0.1rem 0 0.25rem 0.1rem;
}
.introduce-child span:last-child {
    margin-left: 0.35rem;
    font-weight: 700;
    font-size: 0.30rem;
}
.wathet {
  display: inline-block;
  padding: 0.15rem 0.7rem;
  background-color: rgba(60, 155, 255, 0.1);
  border-radius: 33px;
  color: #3C9BFF;
}
/*表格样式--head*/
.checkRecord-table {
  padding-left: 160px;
  padding-right: 30px;
}
.from {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.62rem;
  /*width: 88.5vw;*/
  border-bottom: 1px solid #eee;
  margin: 0 auto;
}
.from .equals {
    width: 25%;
    height: 100%;
    background-color: rgba(60, 155, 255, 0.05);
}
.from .equals:nth-of-type(2) {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 75%;
}
.from .equals .sp {
      width: 33%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
.from .equals .sp:nth-of-type(2) {
        width: 67%;
}
.from .equals .title {
      height: 0.6rem;
      line-height: 0.6rem;
      font-family: PingFangSC-Regular;
      font-size: 0.32rem;
      color: #333333;
      letter-spacing: 0;
      text-align: center;
}
.from .equals .value {
      height: 1.02rem;
      line-height: 1.02rem;
      font-family: PingFangSC-Medium;
      font-size: .32rem;
      color: #333333;
      letter-spacing: 0;
      text-align: center;
      font-weight: 600;
      background-color: #fff;
}
.from .equals .value p {
        display: inline-block;
        width: auto;
        line-height: 0.42rem;
        padding: 0.04rem 0.24rem;
        background: #d43c33;
        border-radius: 0.25rem;
        color: #fff;
        height: 0.5rem;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        vertical-align: middle;
}
.footer_btn {
  padding-left: 160px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  text-align: center;
  margin-top: -0.3rem;
}
.footer_btn img {
    width: 3.5rem;
    height: 0.9rem;
}
.toast_wrap {
  background-color: #5f6164;
  width: 100%;
  height: 110%;
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 99;
  padding-left: 160px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}
.toast_wrap .toast {
    width: 70%;
    background-color: #ffffff;
    border-radius: 10px;
    padding-top: .5rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: hidden;
}
.toast_wrap .toast .toast_top {
      width: 100%;
      text-align: center;
}
.toast_wrap .toast .toast_top span {
        color: #000000;
        font-size: 0.36rem;
        font-weight: bolder;
        width: 100%;
        text-align: center;
        display: inline-block;
}
.toast_wrap .toast .toast_top textarea {
        width: 94%;
        display: inline-block;
        color: #333;
        border: 1px solid #b4b4b4;
        border-radius: 10px;
        height: 3rem;
        padding: .2rem 0 0 .2rem;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        font-size: 0.28rem;
        margin-top: .5rem;
}
.toast_wrap .toast .toast_top textarea:focus {
          outline: none;
}
.toast_wrap .toast .toast_bottom {
      width: 100%;
      border-top: 1px solid #EDEDED;
      margin-top: 0.3rem;
}
.toast_wrap .toast .toast_bottom div {
        width: 50%;
        height: 1rem;
        text-align: center;
        line-height: 1rem;
        float: left;
        border-right: 1px solid #EDEDED;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
}
.toast_wrap .toast .toast_bottom div span {
          font-size: 0.36rem;
          font-weight: 700;
}
.toast_wrap .toast .toast_bottom div.close span {
          color: #5f5f5f;
}
.toast_wrap .toast .toast_bottom div.check {
          border: none;
}
.toast_wrap .toast .toast_bottom div.check span {
            color: #3C9BFf;
}
.doctorAdvice {
  padding: 0.2rem 30px 0.2rem 160px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
}
.doctorAdvice .Advice span:nth-child(2) {
    font-weight: 700;
    margin-left: 0.35rem;
    font-size: 0.3rem;
}
.doctorName {
  text-align: right;
  padding-right: 5%;
}
.doctorName span {
    font-size: 0.36rem;
}
.doctorName span:nth-child(1) {
      font-size: 0.28rem;
}
.doctorName span:nth-child(2) {
      font-weight: 700;
      margin-left: 1%;
}
.change {
  margin-top: 0;
}
/*表格样式--end*/
